<template>
     <div>
        <div class="tile">
            <div class="tile is-parent is-vertical">
                <article class="tile is-child notification is-primary">
                <p class="title">Bonuri carburant: {{nrFuels}}</p>
                <p class="subtitle"><button class="button is-small" @click="addFuel">+ adauga</button></p>
                </article>
            </div>
        </div>       
        
        <loading-info/>

      <table  v-show="fuels.length > 0 && !isLoading"  class="table is-striped is-fullwidth is-hoverable is-responsive">
            <thead>
                <tr>
                <th>Id</th>
                <th>Nr. auto</th>
                <th>Data</th>
                <th>Km</th>
                <th>Litri</th>
                <th>Pret/Litru</th>
                <th>%</th>
                <th>Total</th>
                <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-show="fuels.length > 0" v-for="f in fuels" :key="f.id">
                <th>{{f.id}}</th>
                <td>{{f.vehicleName}}</td>
                <td>{{f.date}}</td>
                <td>{{f.kms}}</td>
                <td>{{f.litres}}</td>
                <td>{{f.litrePrice}}</td>
                <td>{{f.fuelConsumption}} %</td>
                <td>{{f.price}}</td>
                <td>
                    <button @click="edit(f)">edit</button>
                    <button @click="del(f)">sterge</button>
                </td>
                </tr>
                
            </tbody>
        </table>
        <div>
            <div v-show="fuels.length == 0" class="has-text-centered is-size-3">
                <h1 class="is-size-5">Nu am gasit nici un bon de consum.</h1>    
                <div><button class="button is-text"  @click="addFuel"> Adauga un bon de consum</button></div>               
            </div>
        </div>
      <div v-show="!isLoading">
          <button class="button" @click="prev">  inapoi </button>
          &nbsp;
          <button class="button" @click="next"> inainte  </button>
      </div>
    </div>
</template>
<script>
import FuelForm from "@/components/FuelForm";
import LoadingInfo from "@/components/LoadingInfo"

import { mapGetters, mapActions } from "vuex";

export default {
  name: "dashboard",
  components:{LoadingInfo},
  data(){
      return{page:1}
  },
  computed: {
    ...mapGetters(['fuels',"nrFuels",'isLoading']),    
  },

  methods: {
    ...mapActions(["loadFuels", "delFuel", "setEditId"]),
    next(){
        this.page+=1;
        this.loadFuels(this.page);
    },
    prev(){
        if (this.page == 1) return;
        this.page-=1;
        this.loadFuels(this.page);
    },
    addFuel: function() {
      this.setEditId(null);

      this.$modal.open({
        parent: this,
        component: FuelForm,
        hasModalCard: true,
        canCancel:false
      });
    },
    del(f) {
      this.$dialog.confirm({
        message: `Sigur stergem bonul de carburant: ${f.id} din ${
          f.date
        } in valoare de ${f.price} lei ?`,
        onConfirm: () => {
          this.delFuel(f.id)
            .then(() => {
              this.loadFuels();
              this.$toast.open("Bon carburant sters!");
            })
            .catch(err => console.log(err));
        }
      });
    },
    edit: function(f) {
      this.setEditId(f.id);
      this.$modal.open({
        parent: this,
        component: FuelForm,
        hasModalCard: true,
        canCancel:false
      });
    }
  },
  created() {
    this.loadFuels();
  }
};
</script>
