<template>
    <div>
      
      <div class="tile">
            <div class="tile is-parent is-vertical">
              <article class="tile is-child notification is-primary">
                <p class="title">Vehicule: {{nrVehicles}} si bonuri: {{nrFuels}} </p>
                <p class="subtitle">
                      <button  class="button is-small" @click="addVehicle">+ autovehicul</button> &nbsp;
                      <button class="button is-small" @click="addFuel">+ carburant</button></p>
              </article>
            </div>
      </div>

    <dash-item v-for="v in dashboard" :vehicle="v" :key="v.id"/>

    <div class="is-size-3" v-show="dashboard && dashboard.length == 0 && !isLoading">Nu ai nici o inregistrare. Adauga vehicule si bonuri de consum pentru avedea graficele</div>

      <loading-info />

    </div>
</template>

<script>
import VehicleForm from "@/components/VehicleForm";
import FuelForm from "@/components/FuelForm";
import DashItem from "@/components/DashItem";
import LoadingInfo from "@/components/LoadingInfo"

import { mapGetters, mapActions } from "vuex";

export default {
  name: "dashboard",
  components: { DashItem, LoadingInfo },
  computed: {
    ...mapGetters([
      "nrVehicles",
      "dashboard",
      "nrFuels",
      "noVehicles",
      "isLoading"
    ]),
    fuelsTitle() {
      return this.nrFuels && this.nrFuels.length == 1
        ? "1 bon carburant"
        : `${this.nrFuels.length} bonuri` + ` carburant`;
    },
    vehiclesTitle() {
      return this.nrVehicles && this.nrVehicles.length == 1
        ? "1 autovehicul"
        : `${this.nrVehicles.length} autovehicule`;
    },
    time(){return Date.now()}
  },
  methods: {
    ...mapActions(["loadDashboard", "setEditId"]),
    addVehicle: function() {
      this.setEditId(null);

      this.$modal.open({
        parent: this,
        component: VehicleForm,
        hasModalCard: true,
        canCancel:false
      });
    },
    addFuel: function() {
      this.setEditId(null);

      this.$modal.open({
        parent: this,
        component: FuelForm,
        hasModalCard: true,
        canCancel:false
      });
    }
  },
  created() {
    this.loadDashboard();
  }
};
</script>

<style>
.card {
  margin-bottom: 45px;
}
</style>
