import Vue from 'vue'
import Vuex from 'vuex'

import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    phone:'',
    fTypes: [],
    vehicles:[],
    fuels:[],
    dashboard : [],
    totalVehicles:0,
    totalFuels:0,
    editId:null,
    isLoading:false
  },
  getters:{
    fuelTypes: (store) => store.fTypes,
    vehicles:(store) => store.vehicles,
    fuels:(store) => store.fuels,
    nrVehicles:(store) => store.totalVehicles,
    nrFuels:(store) => store.totalFuels,
    currentPhone:(store) => store.phone,
    isAuthenticated:(store) => store.phone && store.phone.length > 0 ,
    dashboard:(store) => store.dashboard,
    editId:(store) => store.editId,
    isLoading:(store) => store.isLoading > 0,
  },
  mutations: {
    SET_PHONE:(store, phone)=> store.phone =  phone,
    SET_TYPES: (store, types) => {
       store.fTypes = types
    },
    SET_VEHICLES: (store, vehicles) => store.vehicles = vehicles,
    SET_FUELS: (store, fuels) => store.fuels = fuels,
    SET_DASHBOARD: (store, dash) =>{ 
      store.totalVehicles = dash.vehicles;
      store.totalFuels = dash.fuels;
      store.dashboard = dash.data;
    },
    SET_EDIT_ID: (store,id) => store.editId = id,
    SET_IS_LOADING: (store,nr) => store.isLoading += nr,
  },
  actions: {
    setPhone({commit,dispatch}, phone){
      Vue.axios.defaults.headers.common["phone"] = phone;
     commit("SET_PHONE",phone);
     dispatch("loadFuels");
     dispatch("loadVehicles");
    },
    logout({commit}){
      commit("SET_PHONE","");
      commit("SET_VEHICLES",[]);
      commit("SET_FUELS",[]);
    },
    loadInitial({commit}) {
      Vue.axios.get('/api/types')
        .then(resp => commit("SET_TYPES", resp.data))
        .catch(err => console.error(err))
    },
    loadVehicles({commit}) {
      commit("SET_IS_LOADING",1);
      Vue.axios.get("/api/vehicles")
        .then((resp) => {
          commit("SET_VEHICLES", resp.data);
          commit("SET_IS_LOADING",-1);
              })
        .catch((err) => {console.error(err);commit("SET_IS_LOADING",-1);});
    },
    loadDashboard({commit}) {
      commit("SET_IS_LOADING",1);
      Vue.axios.get("/api/dashboard")
        .then((resp) => {                    
          var dta = resp.data.items.map((x) =>{
              var obj  =  {items :{}};
              obj.id = x.vehicle.id;
              obj.name = x.vehicle.name;
              obj.hasData = (x.fuels || []).length > 0;
                obj.fuels = (x.fuels || []).map((f) =>{
                    var o = {};
                    obj.items[f.date] = f.consumption;
                    return o;
                } );
                return obj;
              });
          commit("SET_DASHBOARD", {data: dta, vehicles:resp.data.vehicles, fuels: resp.data.fuels});
          commit("SET_IS_LOADING",-1);
        })
        .catch((err) => {console.error(err);commit("SET_IS_LOADING",-1);});
    },
    saveVehicle({commit}, vehicle) {
      //console.log('vehicle to save: ', vehicle);
      return vehicle.id > 0 ? 
          Vue.axios.patch("/api/vehicles", vehicle):
          Vue.axios.post("/api/vehicles", vehicle);
    },
    delVehicle({commit}, id){
      return Vue.axios.delete(`/api/vehicles/${id}`);
    }, 
    saveFuel:({commit}, fuel) =>{ 
    //  commit("SET_IS_LOADING",1);
        
        return fuel.id > 0 ? 
            Vue.axios.patch(`/api/fuels`, fuel):
            Vue.axios.post(`/api/fuels`, fuel); 
    },
    loadFuels({commit}, page) {
      commit("SET_IS_LOADING",1);
      page = page || 1;
      Vue.axios.get(`/api/fuels?page=${page}`)
        .then((resp) => {commit("SET_FUELS", resp.data);commit("SET_IS_LOADING",-1);})
        .catch((err) => {console.error(err);commit("SET_IS_LOADING",-1);});
    },
    delFuel({commit}, id){
      return Vue.axios.delete(`/api/fuels/${id}`);
    },
    setEditId({commit},id){
      commit("SET_EDIT_ID", id)
    },
    loadVehicle({commit}, id){
      commit("SET_IS_LOADING",1);
      return new Promise(function(resolve, reject){
        return  Vue.axios.get(`/api/vehicles/${id}`)
        .then(resp=>{
            commit("SET_IS_LOADING",-1);          
            return resolve(resp);
        })
        .catch(err => {
          commit("SET_IS_LOADING",-1);
          return reject(err)});
      });

    },
    loadFuel({commit}, id){
      commit("SET_IS_LOADING",1);
      var promise = new Promise(function(resolve, reject) {
        return  Vue.axios.get(`/api/fuels/${id}`)
        .then(resp=>{
            commit("SET_IS_LOADING",-1);          
            return resolve(resp);
        })
        .catch(err => {
          commit("SET_IS_LOADING",-1);
          return reject(err)});
      });
      return promise;      
    },
  }
})
